import server from '../../server.js';
export default {
	data() {
		return {
			InputBottom: 0,
			msgid:0,
			MsgList:[],
			auid:0,
			val:'',
			toid:0,
			value:""
		}
	},
	onLoad:function(e){
		this.auid=server.userinfo.Auid
		console.log(e.id,e.toid)
		this.msgid=e.id
		this.toid=e.toid
		this.getMyMsg()
		
	},
	onShow:function(){
		this.markmsg();
	},
	methods: {
		input:function(e){
			this.val=e.target.value 
		},
		getbie:function(){
			uni.navigateTo({
				url: '../i/bieren?auid='+this.toid
			})
		},
		getme:function(){
			uni.navigateTo({
				url: '../i/i'
			})
		},
		upmsg:function(){
			uni.showLoading({
				title: '发送中..',
				mask:true
			});
			if(this.val==''||this.val==null){
				uni.showToast({
					title: "请输入发送内容！",
					position:'bottom',
					icon:'none'
				})
				return;
			}
			uni.request({
				method:'POST',
				url: "https://api.angeli.top/user.php?type=upmsg", //仅为示例，并非真实接口地址。
				data: {
					'msg':this.val,
					'toid':this.toid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'system':server.system
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.value="";
						this.val=''
						this.getMyMsg()
					}
					if(res.data.code=="0" ||res.data.code==null||res.data.code==false ){
						uni.showToast({
							title: "发送失败，请稍后再试",
							position:'bottom',
							icon:'none'
						})
					}
					console.log(this.MsgList)
				},
				complete() {
					uni.hideLoading();
				}
			});
		},
		markmsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=mark&class=msg", //请求标记已读消息
				data: {
					'msgId':this.msgid,
					token:server.token 
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						console.log('已将信息标记为已读')
					}else{
						console.log('标记已读失败！')
					}
					
				},
				complete() {
					
				}
			});
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		getMyMsg:function(){
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=getMyMsg", //仅为示例，并非真实接口地址。
				data: {
					'msgid':this.msgid,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						this.MsgList=res.data.data
						this.toid=this.MsgList[0].ff.Auid
						setTimeout(function () {
							uni.pageScrollTo({
							    scrollTop: 30000,
							    duration: 300
							});
							uni.pageScrollTo({
							    scrollTop: 30100,
							    duration: 300
							});
						}, 200);
					}
					if(res.data.code=="0" ||res.data.data.length<50 ){
						
					}
					console.log(this.MsgList)
				},
				complete() {
					uni.hideLoading();
					
					
				}
			});
		},
	}
}
